@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;900&family=PT+Serif&display=swap);
.App {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-around;
}

.header {
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    font-family: serif;
    background-color: #000;
    color: #fdfdfd;
}

.header > h1 {
    margin-left: 20px;
}

.header > button {
    margin: 0 20px;
    font-family: 'Montserrat', sans-serif;
    color: #fdfdfd;
    outline: none;
    border-bottom: 1px solid transparent;
    padding-bottom: 8px;
}

.header > button:hover {
    border-color: #fdfdfd;
}

.App > section {
    margin-left: 120px;
}

body {
  margin: 0;
  font-family: 'PT Serif', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

.wmde-markdown {
  font-family: 'PT Serif', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

ol {
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', sans-serif;
  color: inherit;
}

button > h1 {
  color: #fdfdfd;
  outline: none;
  border-bottom: 1px solid transparent;
  padding-bottom: 8px;
}

button > h1:hover {
  border-color: #fdfdfd;
}

p {
  font-family: 'PT Serif', serif;
  font-size: 1.2rem;
}

section {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

li {
  font-size: 18px;
  padding: 4px;
  margin: 4px;
  cursor: pointer;
}

li > button {
  padding: 4px;
  border-bottom: 1px solid #000;
  transition: 0.3s all ease-in-out;
  text-align: left;
  width: 40ch;
}

li > button:hover {
  background-color: #000;
  color: #fdfdfd;
  border-bottom: 1px solid #fdfdfd;
}

a {
  text-decoration: none;
}

.article {
  width: 60%;
  padding: 40px 0;
}

.sidemenu {
  position: absolute;
  width: 100%;
  top: 119px;
  z-index: 1;
  background: #000;
  padding: 20px;
  color: #fdfdfd;
}

.sidemenu > section > ol {
  display: flex;
  flex-basis: 33%;
  flex-wrap: wrap;
}

.sidemenu > section > ol > li {
  margin: 0 16px;
}

.sidemenu > section > ol > li > button {
  color: #fdfdfd;
}

img {
  width: 800px;
}

